import { Scene, PerspectiveCamera, WebGLRenderer, BoxGeometry, MeshBasicMaterial, Mesh, Renderer, Camera, RedFormat } from 'three';
import { isWebGLAvailable, getWebGLErrorMessage } from './webgl-checker';

if (!isWebGLAvailable()) {
    let warning: HTMLDivElement = getWebGLErrorMessage();
    document.body.appendChild(warning);
    console.error(warning.innerText);
}

const scene = new Scene();
const camera = new PerspectiveCamera(75, window.innerWidth / window.innerHeight,
                                    0.1, 1000);
const renderer = new WebGLRenderer();

let geometry = new BoxGeometry();
let material = new MeshBasicMaterial({ color: 0xffff00 });
let cube = new Mesh(geometry, material);

scene.add(cube);

camera.position.z = 5;

renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

function animate(): void {
    requestAnimationFrame(animate);

    cube.rotation.x += 0.05;
    cube.rotation.y += 0.01

    renderer.render(scene, camera);
}

function onWindowResize(): void {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
}

window.addEventListener('resize', onWindowResize, false);
animate();
