export function isWebGLAvailable(): Boolean {
	try {
		let canvas = document.createElement('canvas');
		return !!(window.WebGLRenderingContext
					&& (canvas.getContext('webgl') || canvas.getContext('experimental-webgl')));
	} catch (e) {
		return false;
	}
}

export function isWebGL2Available(): Boolean {
	try {
		let canvas = document.createElement('canvas');
		return !!(window.WebGL2RenderingContext && canvas.getContext('webgl2'));
	} catch (e) {
		return false;
	}
}

export function getWebGLErrorMessage(): HTMLDivElement {
	return getErrorMessage(1);
}

export function getWebGL2ErrorMessage(): HTMLDivElement {
	return getErrorMessage(2);
}


export function getErrorMessage(version: number): HTMLDivElement {
	if (version > 2 || version <= 0) {
		console.error("Version should be 1 or 2");
	}

	let name: string = (version === 1) ? "WebGL" : "WebGL 2";
	let context = (version === 1) ? window.WebGLRenderingContext : window.WebGL2RenderingContext;
	let hw: string = (context) ? 'graphics card' : 'browser';
	let message: string = `Your ${hw} does not seem to support <a href="http://khronos.org/webgl/wiki/Getting_a_WebGL_Implementation" style="color:#000">${name}</a>`;
	let element: HTMLDivElement = document.createElement('div');
	element.id = 'webglmessage';
	element.style.fontFamily = 'monospace';
	element.style.fontSize = '13px';
	element.style.fontWeight = 'normal';
	element.style.textAlign = 'center';
	element.style.background = '#fff';
	element.style.color = '#000';
	element.style.padding = '1.5em';
	element.style.width = '400px';
	element.style.margin = '5em auto 0';
	element.innerHTML = message;
	return element;
}
